@use 'functions' as *;
@forward 'functions';

@use 'sass:map';
@use 'sass:math';
@use 'sass:color';

$default-opacity:  0.75;
$header-height: 70px;

:root {
    --footer-height: 80px;
}

$side-gap: 15%; // Keeps site aligned better
$site-radius: 1.2rem;

$dark-theme: 'body.dark &';
$light-theme: 'body.light &';
$shadow-dark-theme: ':host(.dark) &';
$shadow-light-theme: ':host(.light) &';

$themes: (
    'dark': (
        'primary-background': hsl(210, 22%, 4%),
        'secondary-background': hsl(220, 5%, 13%),
        'primary-text': rgb(232, 230, 227)
    ),
    'light': (
        'primary-background': #FAFAFA,
        'secondary-background': #e9e9ed80,
        'primary-text': #313441
    )
);

// Adds a new property in every theme ending with '-transparent'
$themes: set-transparent-backgrounds($themes);